import Game from "@/Game"
import util from "@/util"

export default class Dice {
  private _value: number = 1
  public max: number
  private _locked: boolean = false
  private isRolling: boolean = false
  private valueUsed: boolean = true // True when dice has been rolled but its value has not yet been used

  private game: Game

  // @ts-ignore
  constructor(max: number, game: Game = undefined) {
    this.max = max
    this.game = game
  }

  load(game: Game) {
    this.game = game
  }

  /** Returns the dice value and marks this dice as used until it is rolled again. */
  useValue(): number {
    this.valueUsed = true
    return this.value
  }

  get isUsed(): boolean {
    return this.valueUsed
  }

  roll() {
    if (this.isRolling || this.locked) return
    this.isRolling = true
    this.rollingDice(400)
  }

  private rollingDice(durationMs: number) {
    let rolls = 9
    let t0: number = performance.now()
    const helper = (timestamp: number) => {
      let timePassed = timestamp - t0
      let fraction = 1 - util.clamp((timePassed / durationMs), 0, 1)
      this._value = Math.round(fraction * rolls)

      if (timePassed < durationMs) {
        requestAnimationFrame(helper)
      } else {
        this._value = Math.floor(Math.random() * this.max) + 1
        this.valueUsed = false
        this.game.notifyDiceRoll(this.value)
        this.isRolling = false
      }
    }
    requestAnimationFrame(helper)
  }

  set locked(value: boolean) {
    this._locked = value
  }

  get locked(): boolean {
    return this._locked || !this.isUsed
  }

  get value(): number {
    return this._value
  }

  get rolling(): boolean {
    return this.isRolling
  }

  /*static findDuplicates(values: number[]): number[][] {
    return []
  }*/
}
