import Save from "@/Save"
import * as localforage from "localforage"
import { deserialize, serialize } from "serializr"

export default class SaveSystem {
  private saveCache: {[key: string]: Save} = {} // Working copy of all saves. This is a map from save name to save object.
  private saveStore: LocalForage = localforage.createInstance({
    name: "ditzydice",
    version: 1.0,
    storeName: "saves",
    driver: localforage.INDEXEDDB
  })

  // Backwards compatibility flag
  private loadedFromLocalStorage: boolean = false

  public async save(save: Save) {
    this.saveCache[save.name] = save
    try {
      await this.writeCacheToDB()
    } catch (err) {
      return Promise.reject(err)
    }

    // If there was no error, delete local storage save if existing.
    if (this.loadedFromLocalStorage) {
      console.log("Saving completed. Deleting local storage save.")
      this.writeCacheToLocalStorage("blushingdefeat-saves-old")
      localStorage.removeItem("blushingdefeat-saves")
    }

  }

  /**
   * Returns the save. Rejects if no save with that name was found.
   * @param name
   */
  public async load(name: string): Promise<Save> {
    let localStorageCache = localStorage.getItem("blushingdefeat-saves")
    if (localStorageCache != undefined) {
      this.loadedFromLocalStorage = true
      this.saveCache = this.loadAllFromStorage()
      console.log("Local storage save detected (<=1.5.4). This will be converted to indexeddb on save (1.5.5).")
    }

    try {
      if (this.saveCache == undefined || Object.keys(this.saveCache).length == 0) this.saveCache = await this.readCacheFromDB()
    } catch (err) {
      return Promise.reject(err)
    }

    if (name in this.saveCache) {
      return this.saveCache[name]
    } else return Promise.reject(Error(`No save with the name "${name}" was found.`))
  }

  // INDEXED DB

  private async writeCacheToDB() {
    try {
      await Promise.all(
        Object.keys(this.saveCache).map(saveName =>
          this.saveStore.setItem(saveName, serialize(Save, this.saveCache[saveName]))
        )
      )
    } catch (err) {
      alert(`There was an error writing the save file to storage:\n\n ${err.msg}`)
      console.error(err)
    }
  }

  private async readCacheFromDB(): Promise<{[key: string]: Save}> {
    try {
      // Get the names of all saves
      const saveNames = await this.saveStore.keys()

      // Get all the saves in string format
      let saveStrings: string[] = await Promise.all(
        saveNames.map(saveName => this.saveStore.getItem(saveName))
      ) as string[]

      // Store all saves in the list as a map instead
      return saveStrings.reduce((map: {[key: string]: Save}, saveString) => {
        const save: Save = deserialize<Save>(Save, saveString)
        map[save.name] = save
        return map
      }, {})
    } catch (err) {
      alert(`There was an error reading the save file from storage:\n\n ${err.message}`)
      console.error(err)
      return Promise.reject(err)
    }
  }

  // LOCAL STORAGE

  private deleteFromLocalStorage(name: string) {
    delete this.saveCache[name]
    localStorage.setItem("blushingdefeat-saves", JSON.stringify(this.saveCache))
  }

  private async writeCacheToLocalStorage(key: string) {
    localStorage.setItem(key, JSON.stringify(this.saveCache))
  }

  /*private readFromLocalStorage(name: string): Save {
    return SaveSystem.objectToSave(this.saveCache[name])
  }*/

  private loadAllFromStorage(): {[key: string]: Save} {
    let savesString = localStorage.getItem("blushingdefeat-saves")
    let saves: {[key: string]: Save} = {}
    if (savesString != null) {
      saves = JSON.parse(savesString)
      for (let key of Object.keys(saves)) {
        // @ts-ignore
        // Re-deserialize since for some reason the plain object can't be deserialized directly anymore.
        saves[key] = deserialize(Save, serialize(Save, saves[key]))
      }
    }
    return saves
  }

  // STATIC

  public static objectToSave(saveString: string): Save {
    const save = deserialize(Save, saveString) as unknown as Save
    console.log("Object to save:", save)
    return save
  }
}
